s.type = "text/javascript";
s.src = "https://maps.googleapis.com/maps/api/js?key=AIzaSyAjNWKR1c0gyMiOx-QsB9Dw9EdpGUBrgw8&callback=dm.initMap";

document.head.appendChild(s);



